.ec_content .shopifyPublish_section .nav.nav-tabs .nav-item {
    margin-left: 0;
}

.ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link {
    border-bottom: 1px solid transparent;
    margin-bottom: -1px;
}

.ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link{
    font-size: 14px;
    font-weight: 500;
    color: #202223;
}

.ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link.active {
    border: 1px solid #E6ECEF;
    border-bottom: 1px solid #ffffff;
    border-radius: 3px;
    color: #4E69ED !important;
}

.ec_content .shopifyPublish_section .tab-content {
    border: 1px solid #E6ECEF;
    border-top: none;
    padding: 43px 29px;
    height: auto;
}

.galleries_table table th:first-child, .galleries_table table td:first-child {
    padding-left: 0;
}

.publish_content {
    padding-right: 15px;
    width: calc(100% - 200px)
}

.publish_content h4, 
.autoPublish_heading h4{
    font-size: 16px;
    color: #2C313B;
    margin-bottom: 8px;
    font-weight: 600;
}

.publish_content p,
.autoPublish_heading p{
    font-size:13px;
    font-weight:400;
    margin-bottom: 0;
    color: #6D7175;
}

.manage_galleries {
    margin-bottom: 30px;
}

.autoPublish_heading{
    margin-bottom: 30px;
}

.shopifyEmbedPreview, .shopifyEmbedDelete {
    border: 1px solid #E4E6EF !important;
    padding: 2px !important;
    width: 31px !important;
    height: 31px !important;
}

.shopifyEmbedPreview:hover, .shopifyEmbedPreview:focus, .shopifyEmbedPreview.active, 
.shopifyEmbedDelete:hover, .shopifyEmbedDelete:focus, .shopifyEmbedDelete.active{
    border-color:#4E69ED !important;
    background: transparent !important;
}

.shopifyEmbedPreview:hover svg path, .shopifyEmbedPreview:focus svg path, .shopifyEmbedPreview.active svg path, 
.shopifyEmbedDelete:hover svg path, .shopifyEmbedDelete:focus svg path, .shopifyEmbedDelete.active svg path{
    fill: #4E69ED;
}

.galleries_table th {
    font-size: 14px;
    font-weight: 500;
    color: #2B2B2B;
}

.galleries_table td {
    padding: 15px 0.75rem;
}

.galleries_table table td:first-child {
    color: #333333;
    font-size: 14px;
    font-weight: 600;
}

.galleries_table td {
    font-size: 13px;
    font-weight: 400;
    color: #6D7175;
}

.appExtension_tab h4 {
    font-size: 16px;
    color: #2C313B;
    margin-bottom: 26px;
    font-weight: 600;
}

.extension_steps li {
    margin-bottom: 20px;
    display: flex;
    align-items:flex-start;
}

.extension_steps li span {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background: #6DC15A;
    color: #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 600;
    margin-right: 14px;
    margin-top: 2px;
}

.extension_steps li p {
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    color: #333333;
    margin-bottom: 0;
    width: calc(100% - 35px);
}

.manualPublish_tab h3{
    font-size: 16px;
    color: #2C313B;
    margin-bottom: 8px !important;
    font-weight: 600;
}

.manualPublish_tab .form-group:nth-child(2) {
    max-width: 600px;
    margin-bottom: 15px;
    margin-top: 30px;
}

.ec_content .manualPublish_tab .form-group:nth-child(3) .embed_code_box {
    padding: 0;
}

.ec_content .shopifyPublish_section .manualPublish_tab .form-group:nth-child(3) .tab-content{
    border: none;
    padding: 0;
}

.ec_content .shopifyPublish_section .manualPublish_tab .form-group:nth-child(3) .tab-content textarea {
    font-size: 14px;
    font-weight: 400;
    color: #2B2B2B;
    border: 1px solid #CFCFCF;
    background: #F6F6FA;
    border-radius: 4px;
    min-height: 100px;
}

.ec_content .shopifyPublish_section .manualPublish_tab .form-group:nth-child(3) .tab-content .embed_code .emebed_action{
    bottom: 10px;
    right: 10px;
}

.appExtensionContent {
    display: flex;
}

.extension_steps {
    width: 70%;
    padding-right: 90px;
}

.extension_video {
    width: 30%;
    padding-left: 15px;
}

.extension_video video {
    width: 100%;
    box-shadow: 0 0 6px rgba(0,0,0,0.2);
}

.shopifyWebsiteid_inputArea {
    display: block;
    margin-top: 5px;
    font-size: 14px;
    color: #333333;
    font-weight: 500;
}

.shopifyWebsiteid_inputArea input {
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    color: #2C313B;
    background: #f5f5f6;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    text-align: left;
    max-width: 100px;
    padding: 7px 12px 6px;
    margin-left: 9px;
}

.shopifyWebsiteid_inputArea input::-webkit-input-placeholder{color: #2C313B;}
.shopifyWebsiteid_inputArea input::-moz-placeholder {color: #2C313B;}
.shopifyWebsiteid_inputArea input:-ms-input-placeholder {color: #2C313B;}
.shopifyWebsiteid_inputArea input:-moz-placeholder {color: #2C313B;}

.websiteid_inputOuter {
    max-width: 110px;
    display: inline-block;
}

.websiteid_inputOuter .websiteId_copyButton {
    right: 0px;
    top: 6px;
}

.websiteid_inputOuter .websiteId_copyButton button svg{
    max-width:13px;
}

.shopifyWebsiteid_inputArea #copy-success-alert {
    right: auto;
    left: 230px;
    top: 18px;
}


@media screen and (max-width:1650px){
    .extension_steps{
        padding-right: 30px;
    }

    .extension_steps {
        width: 60%;
    }

    .extension_video {
        width: 40%;
    }
}

@media screen and (max-width:1450px){

    .extension_steps {
        width: 55%;
    }

    .extension_video {
        width: 45%;
    }
}

@media screen and (max-width:1199px){
    .extension_steps {
        width: 70%;
    }
    
    .extension_video {
        width: 30%;
    }
}

@media screen and (max-width:991px){

    .appExtensionContent {
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }

    .extension_steps {
        width: 100%;
        padding-right: 0;
    }

    .extension_video {
        width: 70%;
        margin: 0 auto 50px;
        padding-left: 0;
    }

}

@media screen and (max-width:767px){
    .manage_galleries {
        flex-wrap: wrap;
    }

    .publish_content {
        width: 100%;
        padding-right: 0;
        margin-bottom: 20px;
    }

    .galleries_table table td:last-child {
        width: 100px;
        display: block;
    }

    .ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link{
        padding: 10px 11px;
    }

    .shopifyPublish_section {
        padding-bottom: 40px;
    }

    .ec_content .shopifyPublish_section .tab-content {
        padding: 20px 15px;
    }
}

@media screen and (max-width:575px){

    .ec_content .shopifyPublish_section .nav.nav-tabs{
        margin-bottom: 20px !important;
    }

    .ec_content .shopifyPublish_section .nav.nav-tabs .nav-item,
    .ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link {
        width: 100%;
    }

    .ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link{
        border: 1px solid #E6ECEF;
    }

    .ec_content .shopifyPublish_section .nav.nav-tabs .nav-item .nav-link.active{
        border: 1px solid #4E69ED;
    }

    .ec_content .shopifyPublish_section .tab-content{
        border-top: 1px solid #E6ECEF;
    }

    .extension_video {
        width: 100%;
    }
}

@media screen and (max-width:368px){
    .autoPublish_buttons button {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
    }
}

@media screen and (max-width:360px){
    .shopifyWebsiteid_inputArea input {
        max-width: 100%;
        display: block;
        margin-left: 0;
        margin-top: 10px;
    }
}








